@import '../base.scss';

@import '../components/article/comments';

.c-article-ask-derm {
  position: relative;
  padding-top: 0.875rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 7rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__wrapper {
    position: relative;
    margin-top: 5.8125rem;
    cursor: pointer;
    overflow: hidden;
    display: block;


    &>* {
      pointer-events: none;
    }

    &:hover {
      .c-article-ask-derm__play-button {
        transform: scale(1);
      }
    }
  }

  &__image {
    &-container {
      position: relative;
      padding-bottom: 128%;
    }

    &.is-desktop {
      display: none;

      .has-desktop-only & {
        display: block;
      }
    }
  }

  &__content {
    position: absolute;
    @include t-absolute-center-x-y();
    color: $s-color--font-6;
    width: 80%;
    text-align: center;
  }

  &__pretitle {
    color: $s-color--font-6;
  }

  &__title {
    margin-top: 1.25rem;
    @include t-font--heading-4;
  }

  &__play-button {
    @include t-width-and-height(73);
    display: block;
    margin: 1.875rem auto 0;
    transform: scale(.9);
    transition: transform $s-anim--time-short $s-anim--ease-in-out;

    .icon--play {
      &-inner {
        fill: $s-color--background-tertiary;
      }
    }
  }

  &__pretitle {
    display: none;
  }

  &__comments {
    padding-top: 3.875rem;
    @include t-gutters;
  }

  @media screen and (min-width: 34.375rem) {
    &__image-container {
      padding-bottom: 80%;
    }
  }

  @include t-media-query($s-small-up) {
    &__wrapper {
      margin: 9.375rem auto 0;
      width: 80%;
    }

    &__image-container {
      padding-bottom: 70%;
    }
  }

  @include t-media-query($s-medium-up) {
    &__comments {
      width: 50%;
      margin: 0 auto;
      padding-top: 7.125rem;
    }

    &__image-container {
      padding-bottom: 60%;

      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__play-button {
      width: 6.25rem;
      height: 6.25rem;
      margin-top: 3.375rem;
    }

    &__comments {
      padding-top: 6.125rem;
    }
  }

  @include t-media-query($s-large-up) {
    &__header-background {
      padding-bottom: 8.5625rem;
    }

    &__wrapper {
      margin: 13.8125rem auto 0;
      width: 86%;
    }

    &__image-container {
      padding-bottom: 45.5%;
    }
  }
}