.o-comments {
  max-width: 59.375rem;
  margin: 0 auto;

  &__heading-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }

  &__notice {
    font-size: 0.75rem;
    margin-top: 0.625rem;
  }

  &__alert {
    color: $s-color--font-1;
    margin: 1.125rem 0;
  }

  &__error-item {
    color: $s-color--error;
  }

  &__inputs-group {
    margin-top: 1.25rem;
  }

  &__input {
    width: 100%;
    @include t-font--type-2;
    color: $s-color--font-1;
    padding-bottom: 0.1875rem;
    border-bottom: 0.0625rem solid $s-color--border-secondary;
    margin-bottom: 1.25rem;

    &::placeholder {
      @include t-font--type-5;
      color: $s-color--font-8;
      text-transform: none;
    }
  }

  &__submit {
    margin-left: auto;

    &.o-btn.is-primary {
      display: block;
    }
  }

  &__posted {
    margin-top: 4.4375rem;
  }

  &__comment {
    margin-bottom: 4.25rem;

    &-author {
      display: block;
    }

    &-date {
      display: block;
      color: $s-color--font-8;
    }

    &-body {
      p {
        margin-top: 1.25rem;
        color: $s-color--font-1;
        max-width: 30.375rem;
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__submit {
      width: 16.875rem;
    }
  }
}